import React from 'react';
import * as S from './style';
import Timeline from '../Timeline';
import { VideoInfoMock, TimelineMock } from '../../mock';

function VideoInfo({ player }) {
  const clickfunc = (param) => {
    return () => {
      player.seekTo(param);
    };
  };
  return (
    <S.Container>
      <S.Title>{VideoInfoMock.title}</S.Title>
      <S.Hr />
      <S.DetailContainer>
        <S.Detail>{`: ${VideoInfoMock.view}`}</S.Detail>
        <S.Detail>{` : ${VideoInfoMock.like}`}</S.Detail>
        <S.Detail>{` : ${VideoInfoMock.date}`}</S.Detail>
      </S.DetailContainer>
      <S.TimelineContainer>
        <Timeline title="true"> </Timeline>
        {TimelineMock.map((element, key) => (
          <Timeline
            key={key}
            clickfunc={clickfunc(element.sec)}
            count={element.count}
          >
            {element.label}
          </Timeline>
        ))}
      </S.TimelineContainer>
    </S.Container>
  );
}

export default VideoInfo;
